const app = getApp()
import helper from '../common/helper.js';



export default {
	data() {
		return {
			options: {},
			userInfo: {},
			index: '0',
			payType: 'wechat',
			token: ''
		};
	},
	onLoad(options) {
		this.token = uni.getStorageSync('token')
		this.userInfo = app.globalData.userInfo
		this.options = options
	},
	methods: {
		radioChange(e) {
			console.log(e,'支付类型');
			this.payType = e.detail.value;
		},
		wxAppletPay() {
			// 微信小程序支付
			uni.showLoading({
				title: '支付信息加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/buy-vip`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					member_id: this.options.id,
					app_type: 'miniapp',
					pay_type: 'wechat'
				},
				success: res => {
					// console.log(res);
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.statusCode == 200) {
						this.runRecharge(res.data)
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		},
		runRecharge(payNode) {
			uni.showToast({
				title: "支付加载中...",
				icon: 'loading',
				duration: 1000000
			});
			uni.requestPayment({
				provider: 'wxpay',
				timeStamp: payNode.timeStamp,
				nonceStr: payNode.nonceStr,
				package: payNode.package,
				signType: payNode.signType,
				paySign: payNode.paySign,
				success: res => {
					uni.hideToast()
					res = helper.null2str(res)
					// console.log(res)
					// console.log('success:' + JSON.stringify(res));
					uni.showToast({
						title: "支付成功",
						icon: 'success',
						duration: 2000
					});
					// 两秒后返回上一页
					setTimeout(e => {
						uni.redirectTo({
							url:'/pages/paySuccess'
						})
					}, 2000)
				},
				fail: err => {
					uni.showToast({
						title: "支付失败",
						icon: 'none',
						duration: 2000
					});
				}
			});
		},
		iAgree(app_type) {
			uni.request({
				url: `${helper.requestUrl}/buy-vip`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					member_id: this.options.id,
					app_type: app_type,
					pay_type: this.payType
				},
				success: res => {
					// 调起支付
					console.log(res.data,'zhifu')






				}
			});
		},
		// h5支付
		h5Wxpay(payNode){
			console.log(payNode,'h5')
			wxj.config({
					debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
					appId:payNode.appId, // 必填，公众号的唯一标识
					timestamp: payNode.timeStamp,// 必填，生成签名的时间戳
					nonceStr: payNode.nonceStr, // 必填，生成签名的随机串
					signType: payNode.signType, // 必填，签名，见附录1
					jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
			});
			wxj.ready(function(){  
			   wxj.chooseWXPay({
			     timestamp: payNode.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
			     nonceStr: payNode.nonceStr, // 支付签名随机串，不长于 32 位
			     package: payNode.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
			     signType: payNode.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
			     paySign: payNode.paySign, // 支付签名
			     success: function (res) {
			   		console.log('支付成功')
			       // 支付成功后的回调函数
			     }
			   });
			});
		
		},
		// app 支付
		appWxpay(payNode) {
			console.log(payNode,'-------------------------------------')
			let payTypeWxpay = ''
			if(this.payType == 'wechat'){
				payTypeWxpay = 'wxpay'
			}else{
				payTypeWxpay = 'alipay'
			}
			uni.requestPayment({
			    provider: payTypeWxpay,
			    orderInfo: payNode, //微信、支付宝订单数据
			    success: function (res) {
						console.log(res,'chenggong')
						// 两秒后返回上一页
						setTimeout(e => {
							uni.redirectTo({
								url:'/pages/paySuccess'
							})
						}, 2000)
			    },
			    fail: function (err) {
						console.log(err)
			    },
			});
		}
	}
	
};
